import React from 'react';
import { AreaStack } from '@vx/shape';
import { scaleLinear } from '@vx/scale';
import { extent } from 'd3-array';

export default ({
    width,
    height,
    data,
}) => {
    const keys = Object.keys(data[0]);

    const x = (d, i) => i;

    const xScale = scaleLinear({
        domain: [0, x.length + 1],
        range: [0, width]
    });

    const yScale = scaleLinear({
        domain: [0, Math.max(...data.map((d) => Math.max(...Object.values(d))))],
        range: [0, height]
    });

    console.log('x', [0,1,2].map(xScale));
    console.log('y', data.map(yScale));

    return (
        <svg width={width} height={height}>
            <rect
                x={0}
                y={0}
                width={width}
                height={height}
                fill="#fafafa"
                rx={14}
            />
            <AreaStack
                keys={keys}
                data={data}
                x={(d, i) => xScale(i)}
                y0={(d) => yScale(d[1])}
                y1={(d) => yScale(d[0])}
                fill={() => '#fff'}
                strokeWidth={2}
                stroke={() => '#000'}
                fillOpacity='1'
            />
        </svg>
    );
};


